.dt{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url('../images/salad-gc7f87fae6_1920.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment:fixed;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.imga{
    height: 8cm;
    width: 10cm;
    border-radius: 18px;
    box-shadow: 4px 4px 10px 10px rgba(0, 0, 0, 0.55);    
}

.type{
    background-color:rgb(212, 212, 158) ;
    border-radius: 20px;
    padding: 10px;
    box-shadow: 4px 4px 10px 10px rgba(0, 0, 0, 0.55);    
    font-family: italic;
}